:root {
  --color-white: #faf9fa;
  --color-accent: #1fc7d4;
  --color-purple: #7645d9;
  --color-dark-purple: #280d5f;
  --color-light-purple: #7a6eaa;
  --color-light-purple-2: #b8add2;
  --colors-cardBorder: #e7e3eb;
  --colors-backgroundAlt: #fff;
  --colors-failure: #ed4b9e;
  --colors-home2: #fff;
  --colors-home2-1: #d7caec;
  --colors-gradient: #f4eeff;
  --colors-gradient-back1: #e6fdff;
  --colors-gradient-back2: #f3efff;
  --color-contrast: #fff;
  --color-bar-bg: #fff;
  --color-tertiary: #eff4f5;
  --color-bar-border: #e7e3eb;
  --color-black: #000;
}
body[data-theme="dark"] {
  --color-white: #08060b;
  --color-accent: #1fc7d4;
  --color-purple: #9a6aff;
  --color-dark-purple: #f4eeff;
  --color-light-purple: #b8add2;
  --colors-cardBorder: #383241;
  --colors-backgroundAlt: #27262c;
  --colors-failure: #ed4b9e;
  --colors-home2: #09070c;
  --colors-home2-1: #201335;
  --colors-gradient: #280d5f;
  --colors-gradient-back1: #21193a;
  --colors-gradient-back2: #191326;
  --color-contrast: #191326;
  --color-bar-bg: #27262c;
  --color-tertiary: #353547;
  --color-bar-border: #383241;
  --color-black: #fff;
}
$accent: #1fc7d4;

%scroll-bar {
  &::-webkit-scrollbar {
    background-color: var(--color-white);
    width: 0.6rem;
    border-radius: 0.5rem;

    &-thumb {
      background-color: var(--color-light-purple);
      border-radius: 0.5rem;
    }
  }
}

